import React, { Component } from 'react';
import '../App.css';
import axios from 'axios';
import { Link } from 'react-router-dom';
import Story from "./Story";


class Content extends Component {
  constructor(props) {
    super(props);
    this.state = {
      snips: []
    };
  }

  componentDidMount() {
	  console.log(this.props.match.params.story)
    axios
     // .get(`http://localhost:8082/api/story/${this.props.match.params}`)
     .get(`http://localhost:8082/api/story/${this.props.match.params.story}`)
      .then(res => {
        this.setState({
          snips: res.data
		 
        })
      })
      .catch(err =>{
        console.log('Error from ShowBookList');
      })
  };


  render() {
    const snips = this.state.snips;
    console.log("PrintBook: " + snips);
    let contentList;

    if(!snips) {
      contentList = "there is no snip record!";
	  console.log("no sips");
    } else {
		console.log(snips)
		snips.sort(comp);
		console.log(snips)
      contentList = snips.map((item,k) =>
     
       <p>{item.content}</p>
	 
	  
      );
	  contentList.sort();
    }

    return (
      
	<div className="card-container">
            
            <div className="desc">		
          <div className="list">
              {contentList}
         </div>
		</div> 
        </div>
    );
  }
}

export default Content;

function comp(a,b) {
	console.log(a.publish_date);
	console.log(b.publish_date);
	//return a.publish_date.getTime()-b.publish_date
    return new Date(a.publish_date).getTime() - new Date(b.publish_date).getTime();
}
